import React, { useContext, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthProvider";
import { HttpHeadersContext } from "../context/HttpHeadersProvider";

function BbsWrite() {
    const { auth } = useContext(AuthContext);
    const { headers } = useContext(HttpHeadersContext);

    const navigate = useNavigate();
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
	const [price, setPrice] = useState();

    const [selectedFiles, setSelectedFiles] = useState([]);

    const handleImageChange = (event) => {
        setSelectedFiles([...event.target.files]);
    };

    const changeTitle = (event) => {
        setTitle(event.target.value);
    };

    const changeContent = (event) => {
        setContent(event.target.value);
    };
	const changePrice = (event) => { 
        const inputPrice = event.target.value.replace(/[^0-9]/g, ""); 
        const formattedPrice = inputPrice.replace(/\B(?=(\d{3})+(?!\d))/g, ","); 
        setPrice(formattedPrice);
    };
    const createBbs = async () => {
        if (!auth) {
            alert("      !");
            navigate("/login");
            return;
        }

        if (selectedFiles.length === 0) {
            alert("     !");
            return;
        }

        const tradeBoardDto = {
            title: title,
            content: content,
			price: parseInt(price.replace(/,/g, ''))
        };

        try {
            const response = await axios.post(
                "/api/write",
                tradeBoardDto,
                { headers: headers }
            );

            const tradeBoardId = response.data.id;
            uploadImages(tradeBoardId);
        } catch (error) {
            console.log("[BbsWrite.js] createBbs() error :", error);
        }
    };

    async function uploadImages(tradeBoardId) {
        const formData = new FormData();
        for (let i = 0; i < selectedFiles.length; i++) {
            formData.append("file", selectedFiles[i]);
        }

        try {
            await axios.post(
                `/api/${tradeBoardId}/images`,
                formData
            );
            alert("    :D");
            navigate(`/bbsdetail/${tradeBoardId}`);
        } catch (error) {
            console.log("  :", error);
            alert("    .");
        }
    }

    return (
        <div>
            <table className="table">
                <tbody>
                    <tr>
                        <th className="table-primary"></th>
                        <td>
                            <input
                                type="text"
                                className="form-control"
                                value={title}
                                onChange={changeTitle}
								placeholder=""
                                size="50px"
                            />
                        </td>
                    </tr>
                    <tr>
                        <th className="table-primary"></th>
                        <td>
                            <textarea
                                className="form-control"
                                value={content}
                                onChange={changeContent}
								placeholder=",     "
                                rows="10"
                            ></textarea>
                        </td>
                    </tr>
					<tr>
                        <th className="table-primary"></th>
                        <td>
                            <input
                                type="text"
                                className="form-control"
                                value={price}
                                onChange={changePrice}
                                placeholder="  (: 25,000)"
                            />
                            
                        </td>
                    </tr>
                </tbody>
            </table>
            <div>
                <h2> </h2>
                <input type="file" multiple onChange={handleImageChange} />
            </div>
            <div className="my-5 d-flex justify-content-center">
                <button className="btn btn-outline-secondary" onClick={createBbs}>
                    <i className="fas fa-pen"></i> 
                </button>
            </div>
        </div>
    );
}

export default BbsWrite;
