import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import "../../css/MyTradeInfo.css"

function MyTradeInfo() {
  const [tradeList, setTradeList] = useState([]);
  const [activeTab, setActiveTab] = useState("");
  const memberId = localStorage.getItem("id");

  useEffect(() => {
    if (memberId) {
      fetchTradeList(memberId);
    } else {
      alert("  .");
    }
  }, [memberId]);

  const fetchTradeList = async (id) => {
    try {
      const response = await axios.get(`/api/tradeInfo/${id}`);
      setTradeList(response.data);
      console.log(response);
    } catch (error) {
      console.error("Error fetching trade list:", error);
    }
  };

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  const filteredTradeList = tradeList.filter(trade => {
    if (activeTab === "") {
      return trade.status === "";
    } else if (activeTab === "") {
      return trade.status === "";
    }
    return false;
  });

  return (
    <div className="my-trade-info">
      <h2>  </h2>
      <div className="tabs">
        <button className={`tab ${activeTab === "" ? "active" : ""}`} onClick={() => handleTabChange("")}></button>
        <button className={`tab ${activeTab === "" ? "active" : ""}`} onClick={() => handleTabChange("")}></button>
      </div>
      <div className="trade-list">
        {filteredTradeList.map((trade) => (
          <div key={trade.id} className="trade-card">
            <div className="title">
              <Link to={`/bbsdetail/${trade.id}`} className="bbs-title">
                {trade.title}
              </Link>
            </div>
            <div className="status">
              : {trade.status}
            </div>
            <div className="created-at">
              : {trade.createdAt}
            </div>
          </div>
        ))}
        {filteredTradeList.length === 0 && <p>   .</p>}
      </div>
    </div>
  );
}

export default MyTradeInfo;
