import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function ResetPassword() {
  const navigate = useNavigate();
  const [userId, setUserId] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [memberExists, setMemberExists] = useState(false);

  const handleUserIdChange = (e) => {
    setUserId(e.target.value);
    setMemberExists(false);
    setErrorMessage("");
  };

  const handleNewPasswordChange = (e) => {
    setNewPassword(e.target.value);
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
  };

  const handleFindUser = async () => {
    try {
      const response = await axios.get(`/api/findPassword?userId=${userId}`);
      const memberDto = response.data;

      if (!memberDto) {
        setErrorMessage("  .");
        setNewPassword("");
        setConfirmPassword("");
      } else {
        setErrorMessage("");
        setMemberExists(true);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleResetPassword = async () => {
    try {
      const response = await axios.get(`/api/findPassword?userId=${userId}`);
      const memberDto = response.data;
  
      if (newPassword !== confirmPassword) {
        setSuccessMessage("");
        setErrorMessage("    .");
      } else {
        memberDto.password = newPassword;
  
        await axios.post("/api/findPassword", memberDto);
  
        setSuccessMessage(" .");
        setErrorMessage("");
  
        //     '/' 
        navigate("/");
  
        //     alert  
        alert(" .");
      }
    } catch (error) {
      setSuccessMessage("");
      setErrorMessage("  .");
    }
  };
  

  return (
    <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
      <h2> </h2>
      <div>
        {!memberExists && (
          <div>
            <input
              type="text"
              placeholder=" "
              value={userId}
              onChange={handleUserIdChange}
              style={{ width: "300px", marginBottom: "10px" }}
            />
            <button onClick={handleFindUser}> </button>
          </div>
        )}
        {errorMessage && <p>{errorMessage}</p>}
        {memberExists && (
          <div>
            <input
              type="password"
              placeholder="  "
              value={newPassword}
              onChange={handleNewPasswordChange}
              style={{ width: "300px", marginBottom: "10px" }}
            />
            <input
              type="password"
              placeholder=" "
              value={confirmPassword}
              onChange={handleConfirmPasswordChange}
              style={{ width: "300px", marginBottom: "10px" }}
            />
            <button onClick={handleResetPassword}> </button>
          </div>
        )}
        {successMessage && <p>{successMessage}</p>}
      </div>
    </div>
  );
}

export default ResetPassword;
